import "./styles.css";
import React from "react";
import { useSelect, SelectProvider } from "./SelectContext";
import MySelect from "./MySelect";
import { RETAILERS, EMOTIONS } from "./constants";

const App: React.FunctionComponent = () => {
  const [retailersState, retailersOptions, retailersDispatch] = useSelect(
    RETAILERS,
    RETAILERS
  );
  const [emotionsState, emotionsOptions, emotionsDispatch] = useSelect(
    EMOTIONS,
    EMOTIONS
  );
  return (
    <div className="App">
      <div className="flex-row flex-item flex-center">
        <SelectProvider
          allOptions={retailersOptions}
          selectedOptions={retailersState}
          dispatchSelection={retailersDispatch}
        >
          <MySelect showAllOption truncateValues />
        </SelectProvider>
        <SelectProvider
          allOptions={emotionsOptions}
          selectedOptions={emotionsState}
          dispatchSelection={emotionsDispatch}
        >
          <MySelect showAllOption truncateValues />
        </SelectProvider>
      </div>
      <div
        className="flex-column flex-end flex-item"
        style={{ padding: "1rem" }}
      >
        <p>
          A customized React-Select component that shows icons in place of
          values. Includes an "overflow" number for when the number of selected
          values exceeds the space. Overflow is determined through use of media
          queries. Toggle responsive view and adjust the viewport width to see
          how the overflow changes.
        </p>
        <p>
          Highlights:
          <ul>
            <li>Overflow number appears with values when space is limited</li>
            <li>'All' option in dropdown group</li>
            <li>Customizable to any set of icons</li>
            <li>Single selection shows selection label with icon</li>
            <li>All typed with Typescript</li>
          </ul>
        </p>
        <p>Enjoy! -- Marco</p>
      </div>
    </div>
  );
};

export default App;
