import React from "react";
import { MultiValueProps, components } from "react-select";
import { useMediaQuery } from "react-responsive";
import { ISelectOption } from "../types";
import { getOptions } from "../helpers";
import MySelectSingleValue from "./MySelectSingleValue";

interface IProps {
  truncateValues?: boolean;
}

const MultiValue: React.FunctionComponent<
  MultiValueProps<ISelectOption> & IProps
> = (props) => {
  const xs = useMediaQuery({
    query: "(max-width: 279px)"
  });
  const sm = useMediaQuery({
    query: "(min-width: 280px) and (max-width: 319px)"
  });
  const md = useMediaQuery({
    query: "(min-width: 320px) and (max-width: 354px)"
  });
  const lg = useMediaQuery({
    query: "(min-width: 350px)"
  });
  const { index, getValue } = props;
  const value = getOptions(getValue());
  const count = value.length;
  // controls how many values show
  const displayLimit =
    (!props.truncateValues && 5) ||
    (lg && 5) ||
    (md && 3) ||
    (sm && 2) ||
    (xs && 1) ||
    1;
  // The number of additional values that aren't shown
  const hiddenCount = count > displayLimit ? count - displayLimit : 0;
  const { Icon } = props.data;

  // If only one value use the SingleValue component
  return count === 1 ? (
    <MySelectSingleValue {...props} />
  ) : // If displayLimit hasn't been reached, show the icon
  index < displayLimit ? (
    <components.MultiValue {...props}>
      {Icon ? <Icon /> : null}
    </components.MultiValue>
  ) : // If displayLimit has been reached, show number of unseen values
  index === displayLimit ? (
    <div className="mdfy__values">
      {hiddenCount ? (
        <div className="mdfy__values__plus">+{hiddenCount}</div>
      ) : null}
    </div>
  ) : // Or show nothing
  null;
};

export default MultiValue;
