import React, { useContext, createRef } from "react";
import Select, {
  GroupBase,
  OptionsOrGroups,
  ActionMeta,
  OnChangeValue,
  SelectInstance
} from "react-select";
import { SelectContext } from "./SelectContext";
import * as SelectComponents from "./SelectComponents";
import styles from "./mySelectStyles";
import { ISelectOption } from "./types";
import { AGGSGROUP, OPTIONS } from "./constants";

interface IProps {
  showAllOption?: boolean;
  truncateValues?: boolean;
}

const MySelect: React.FunctionComponent<IProps> = (props) => {
  const { selectedOptions, dispatchSelection, allOptions } = useContext(
    SelectContext
  );

  const options: OptionsOrGroups<
    ISelectOption,
    GroupBase<ISelectOption>
  > = props.showAllOption
    ? [
        AGGSGROUP,
        {
          label: OPTIONS,
          options: allOptions.map((o) => ({
            ...o,
            group: OPTIONS
          }))
        }
      ]
    : allOptions;

  const handleChange = (
    selected: OnChangeValue<ISelectOption, boolean>,
    action: ActionMeta<ISelectOption>
  ) => {
    dispatchSelection({
      ...action,
      options: selected ? (selected as OnChangeValue<ISelectOption, true>) : []
    });
  };

  const ref: React.RefObject<SelectInstance<ISelectOption>> = createRef();

  return (
    <Select
      ref={ref}
      options={options}
      defaultValue={selectedOptions}
      value={selectedOptions}
      //styling
      className="mdfy-selector"
      classNamePrefix="mdfy"
      styles={styles}
      // multi props
      isMulti
      isClearable={false}
      isSearchable={false}
      blurInputOnSelect={false}
      closeMenuOnSelect={false}
      // control
      controlShouldRenderValue
      // menu
      hideSelectedOptions={false}
      // components
      components={{
        Option: SelectComponents.Option,
        Group: SelectComponents.Group,
        MultiValue: (componentProps) => (
          <SelectComponents.MultiValue
            {...componentProps}
            truncateValues={props.truncateValues}
          />
        ),
        SingleValue: SelectComponents.SingleValue,
        MultiValueRemove: SelectComponents.NullRender,
        IndicatorSeparator: SelectComponents.NullRender
      }}
      // interaction
      onChange={handleChange}
    />
  );
};

export default MySelect;
