import React, {
  Dispatch,
  Reducer,
  createContext,
  useReducer,
  PropsWithChildren
} from "react";
import { ActionMeta, MultiValue } from "react-select";
import { IOption, ISelectOption } from "./types";
import { ALLOPTION, OPTIONS } from "./constants";
import { getOptions, orderOptions } from "./helpers";

export type SelectActionType = ActionMeta<ISelectOption>;

type StateType = MultiValue<ISelectOption>;

type Action = SelectActionType & {
  options: MultiValue<ISelectOption>;
};

type DispatchSelection<A = Action> = (action: A) => void;

type ContextType = {
  dispatchSelection: DispatchSelection;
  allOptions: MultiValue<ISelectOption>;
  selectedOptions?: MultiValue<ISelectOption>;
};

export const SelectContext = createContext<ContextType>({
  allOptions: [],
  selectedOptions: [],
  dispatchSelection: () => null
});

const reducerFactory = (allOptions: StateType, allOption: ISelectOption) => {
  const reducer: Reducer<StateType, Action> = (state, action) => {
    const order = allOptions.map((opt) => opt.value);
    switch (action.action) {
      case "select-option":
        // Handle select 'all'
        if (action.option?.value === "all") {
          return [...allOptions, action.option];
        }
        // Handle last deselected option selected
        if (action.options.length >= allOptions.length) {
          return [...allOptions, allOption];
        }
        // Handle option selected
        return orderOptions(action.options, order);
      case "deselect-option":
        // handle last selected option deselected
        if (action.options.length === 0) {
          return [...allOptions, allOption];
        }
        /**
         * Handle option and 'all' removal
         *
         * When all options are selected and an option
         * is deselected, getOptions is needed to remove
         * the 'all' option
         */
        return getOptions(action.options);
      default:
        return [...state];
    }
  };
  return reducer;
};

export const SelectProvider: React.FC<PropsWithChildren<ContextType>> = ({
  children,
  ...props
}) => {
  return (
    <SelectContext.Provider
      value={{
        selectedOptions: props.selectedOptions || [],
        allOptions: props.allOptions || [],
        dispatchSelection: props.dispatchSelection || null
      }}
    >
      {children}
    </SelectContext.Provider>
  );
};

type UseSelection = (
  initialOptions: IOption[],
  allOptions: IOption[]
) => [StateType, StateType, Dispatch<Action>];

export const useSelect: UseSelection = (initialOptions, allOptions) => {
  // Add group names to options
  const optionToSelectOption: (opt: IOption) => ISelectOption = (opt) => ({
    ...opt,
    group: OPTIONS
  });
  const initialState: StateType = [
    ...initialOptions.map(optionToSelectOption),
    ...(initialOptions.length === allOptions.length ? [ALLOPTION] : [])
  ];
  const allSelectOptions: StateType = allOptions.map(optionToSelectOption);

  const r = reducerFactory(allSelectOptions, ALLOPTION);
  const [state, dispatch] = useReducer<Reducer<StateType, Action>>(
    r,
    initialState
  );

  return [state, allSelectOptions, dispatch];
};
