import React from "react";
import { OptionProps, GroupBase, components } from "react-select";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faCircleCheck } from "@fortawesome/free-regular-svg-icons";
import Badge from "../Badge";
import { ISelectOption } from "../types";
import { OPTIONS } from "../constants";

const Option: React.FunctionComponent<OptionProps<
  ISelectOption,
  boolean,
  GroupBase<ISelectOption>
>> = (props) => {
  const { Icon, label } = props.data;
  return props.data.group === OPTIONS ? (
    <components.Option {...props}>
      <div className="label-group">
        {Icon ? <Icon /> : null}
        {label}
      </div>
      {props.isSelected ? (
        <FontAwesomeIcon
          icon={faCircleCheck}
          size="1x"
          color="var(--feature-700)"
        />
      ) : null}
    </components.Option>
  ) : (
    <components.Option {...props}>
      <Badge active={props.isSelected}>{props.children}</Badge>
    </components.Option>
  );
};

export default Option;
