import React from "react";
import { GroupProps, GroupBase } from "react-select";
import { ISelectOption } from "../types";
import { AGGS } from "../constants";

const Group: React.FunctionComponent<GroupProps<
  ISelectOption,
  true,
  GroupBase<ISelectOption>
>> = (props) => {
  return props.label === AGGS ? (
    <div className="aggs-group">{props.children}</div>
  ) : (
    <div>{props.children}</div>
  );
};

export default Group;
