import { OPTIONS } from "./constants";
import { ISelectOption } from "./types";
/**
 ** Returns options that are only part of the 'options' group.
 
 * Description:
 * The 'all' option is part of the 'aggs' group and is added to
 * the list of values. This behavior allows us to know if 'all'
 * is selected so we can style the option. This function gives
 * us a clean options-only list.
 * */

export const getOptions = (options: Readonly<ISelectOption[]>) => {
  return options.filter((opt) => opt.group === OPTIONS);
};
/**
 * Puts options in order based on a values array.
 *
 * Description:
 * Default behavior of a multi-select is to add the selected
 * option to the add of the values list. This gives us inconsistent
 * ordering as the user adds and removes options. This function
 * allows us to maintain the order of values to match the order
 * of options.
 */
export const orderOptions: (
  options: Readonly<ISelectOption[]>,
  order: string[]
) => ISelectOption[] = (options, order) => {
  return order
    .map((value) => options.find((opt) => opt.value === value))
    .filter((opt) => !!opt) as ISelectOption[];
};
